.skillsets {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 95%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.skillsets h1{
    color: white;
}

.skillsets__info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.skillset__box {
    border: 2px solid rgb(207, 42, 86);
    padding: 0.8rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: space-evenly;
       -ms-flex-pack: space-evenly;
    justify-content: space-evenly;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 17rem;
    height: 14rem;
    margin: 1rem 0.5rem;
    -webkit-box-shadow: 1px 1px 1px rgba(207, 42, 86);
            box-shadow: 1px 1px 1px rgba(207, 42, 86);
    -webkit-transition: all ease-in 0.3s;
    -o-transition: all ease-in 0.3s;
    transition: all ease-in 0.3s;
}

.skillset__box:hover{
    -webkit-box-shadow: 0px 0px 15px rgba(207, 42, 86);
            box-shadow: 0px 0px 15px rgba(207, 42, 86);
    -webkit-transform: scale(1.02);
        -ms-transform: scale(1.02);
            transform: scale(1.02);
}

.svg-skill {
    display: inline-block;
    padding-top: 1rem;
}

.svg-skill img {
    width: 7.5rem;
    height: 7.5rem;
}

.more-size {
    font-size: 1.3rem;
    margin: 0.3rem;
}

@media screen and (max-width: 930px) {
    .svg-skill img {
        width: 4.5rem;
        height: 4.5rem;
    }

    .skillset__box {
        padding: 0.8rem;
        width: 9rem;
        height: 9rem;
        margin: 0.75rem 0.40rem;
    }

    .more-size {
        font-size: 0.8rem;
        margin: 0.5rem;
    }
}

@media screen and (max-width: 392px) {
    .skillsets__info {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;

    }

    .skillset__box {
        padding: 0.5rem;
        width: 7.5rem;
        height: 7.5rem;
        margin: 0.4rem 0.40rem;
    }

    .svg-skill img{
        width: 3.5rem;
        height: 3.5rem;
    }

    .more-size {
        font-size: 0.8rem;
        margin: 0.2rem;
    }
}
